import React from "react";
import "./styles/CoinRow.scss";
import { AiOutlineArrowDown, AiOutlineArrowUp } from "react-icons/ai";

const CoinRow = ({ coins }) => {
  return (
    <div className="coin">
      <img className="coinImg" src={coins.image} alt="" />
      <div className="coin-desc">
        <div className="coin-name">
          <p className="name">{coins.name}</p>
          <p className="subname">{coins.symbol}</p>
        </div>
        <div className="coin-info">
          <p className="price">
            {"$" +
              coins.current_price
                .toFixed(2)
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")}
          </p>
          <p
            style={{
              color:
                coins.price_change_percentage_24h > 0 ? "#00E60B" : "#F00F00",
            }}
          >
            {" "}
            <span>
              {coins.price_change_percentage_24h > 0 ? (
                <AiOutlineArrowUp />
              ) : (
                <AiOutlineArrowDown />
              )}
            </span>{" "}
            {coins.price_change_percentage_24h}
          </p>
        </div>
      </div>
    </div>
  );
};

export default CoinRow;
